@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6, button {
  font-family: 'Audiowide', 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.center-page {
  display: flex;
  justify-content: center;
}
.App {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
}

.App>*:nth-child(2) {
  margin-top: 70px;
}

.Things {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100vw;
  height: calc(100vh - 70px);
}
.Thing-Container {
  width: 400px;
  height: 500px;
  background: #eee;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  padding-bottom: 30px;
  position: relative;
  border: 2px solid black;
}

.Thing-Container h2 {
  width: 90%;
  align-self: start;
}

.Thing-Container img, .Thing-Container figure {
  width: 90%;
  height: 200px;
  object-fit: contain;
  margin: 10px 0;
}

.Thing-Container figure {
  background: #aaa;
}

.Thing-Container p {
  text-align: center;
  height: 100px;
}

.Thing-Container button {
  background: #3a3;
  border: none;
  font-size: 1.5rem;
  color: white;
  width: 90%;
}

.Thing-Container .explosion {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.Thing-Container .save-btn {
  position: absolute;
  top: .2rem;
  right: .2rem;
  color: white;
  font-size: .5rem;
  width: 2rem;
  padding: .2rem;
}

.Thing-Container .save-btn * {
  pointer-events: none;
}

.Thing-Container .save-btn img {
  width: 1rem;
  height: 1rem;
  margin: 0;
}

.Thing-Container .save-btn p {
  width: 1.5rem;
  height: 100%;
  margin: 0 auto;
}
.Header {
  height: 60px;
  display: flex;
  justify-content: space-between;
  background: #37a9f0;
  position: fixed;
  width: 100vw;
  top: 0;
}

.Header a {
  text-decoration: none;
}

.Header h1, .Header h3 {
  transition: background .1s linear, color .1s linear;
  height: 100%;
  line-height: 43px;
  padding: 5px 10px 5px 18px;
  text-align: center;
  --shadow-color: black;
  text-shadow: -2px 1px 0 var(--shadow-color), -4px 2px 0 var(--shadow-color), -6px 3px 0 var(--shadow-color), -8px 4px 0 var(--shadow-color);
}

.Header h1 {
  width: 180px;
}

.nav-links {
  display: flex;
}

.nav-links * {
  background: #37a9f0;
  color: #de64de;
}

.nav-links *:visited {
  color: #de64de;
}

.nav-active * {
  color: #37a9f0 !important;
  background: #de64de !important;
}
.Datum>* {
  border-right: 1px solid grey;
  padding: 5px 10px;
  max-width: 250px;
}

.Datum>*:last-child {
  border-right: none;
}
.Data-Display {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  justify-content: center;
}

.chart-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.chartjs-render-monitor {
  width: 90% !important;
  height: 90% !important;
  background: #eee;
  border-radius: 1rem;
  margin: 1rem;
}

.saved-items {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-shadow: 0px 2px 2px white, 0px -2px 2px white, 2px 0px 2px white, -2px 0px 2px white, 2px 2px 2px white, 2px -2px 2px white, -2px 2px 2px white, -2px -2px 2px white;
}

.saved-items ul {
  list-style-type: none;
}

.saved-items ul * {
  list-style-type: none;
  margin: .2rem 0;
}
.Data-Table {
  margin: 0 auto;
  padding: 20px;
}

.Data-Table * {
  text-shadow: 0px 2px 2px white, 0px -2px 2px white, 2px 0px 2px white, -2px 0px 2px white, 2px 2px 2px white, 2px -2px 2px white, -2px 2px 2px white, -2px -2px 2px white;
}

.Data-Table table {
  border-collapse: collapse;
}

.Data-Table h2 {
  text-align: center;
}

.Data-Table p {
  font-size: 1.1rem;
}
.About-Page {
  padding-top: 1rem;
  max-width: 650px;
  margin: 0 auto;
  line-height: 1.4rem;
  font-size: 1.1rem;
  background: whitesmoke;
  padding: 1rem;
}

.About-Page h1 {
  margin-bottom: 3rem;
}

.About-Page br {
  content: "";
  margin: 2rem;
  display: block;
  font-size: 200%;
}
.Game-Background {
  position: fixed;
  top: 60px;
  z-index: -1000;
  background: #aaa;
}

.Game-Background canvas {
  mix-blend-mode: multiply;
}
.Page-404 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: whitesmoke;
  display: inline-block;
  margin: 0 auto;
  text-align: center;
  padding: 1rem;
}

.Page-404 h1 {
  -webkit-animation: flash-red 1s linear infinite alternate;
          animation: flash-red 1s linear infinite alternate;
}

.Page-404 * {
  margin-bottom: 1rem;
}

@-webkit-keyframes flash-red {
  0% {
    color: black;
  }
  100% {
    color: #e55;
  }
}

@keyframes flash-red {
  0% {
    color: black;
  }
  100% {
    color: #e55;
  }
}
